"use client";

import { useState } from "react";
import { Edit3, Trash2, Check, X, Search } from "lucide-react";

// Peso formatter
const currency = (val = 0) =>
  new Intl.NumberFormat("en-PH", {
    style: "currency",
    currency: "PHP",
    minimumFractionDigits: 2,
  }).format(val);

export default function TransactionTable({
  transactions = [],
  loading,
  onEditTransaction,
  onDeleteTransaction,
}) {
  const [editingId, setEditingId] = useState(null);
  const [rowForm, setRowForm] = useState({});

  /* ================= FILTER STATE ================= */
  const [filters, setFilters] = useState({
    type: "all", // all | income | expense
    search: "",
    fromDate: "",
    toDate: "",
  });

  /* ================= FILTER LOGIC ================= */
  const filteredTransactions = transactions.filter((t) => {
    // TYPE
    if (filters.type === "income" && t.deposit <= 0) return false;
    if (filters.type === "expense" && t.withdrawal <= 0) return false;

    // DATE RANGE
    if (filters.fromDate && new Date(t.date) < new Date(filters.fromDate))
      return false;
    if (filters.toDate && new Date(t.date) > new Date(filters.toDate))
      return false;

    // SEARCH (description or payee)
    if (
      filters.search &&
      !`${t.description ?? ""} ${t.payee ?? ""}`
        .toLowerCase()
        .includes(filters.search.toLowerCase())
    )
      return false;

    return true;
  });

  /* ================= EDIT LOGIC ================= */
  const startEdit = (t) => {
    setEditingId(t.id);
    setRowForm({
      date: t.date,
      description: t.description,
      deposit: t.deposit || "",
      withdrawal: t.withdrawal || "",
    });
  };

  const saveEdit = (id) => {
    onEditTransaction(
      {
        date: rowForm.date,
        description: rowForm.description,
        deposit: Number(rowForm.deposit || 0),
        withdrawal: Number(rowForm.withdrawal || 0),
      },
      id
    );
    setEditingId(null);
  };

  return (
  <div className="space-y-4">

    {/* FILTER BAR */}
    <div className="bg-white border rounded-lg px-4 py-3 shadow-sm">
      <div className="flex flex-wrap gap-3 items-center">

        <select
          className="border rounded-md px-3 py-2 text-sm bg-white"
          value={filters.type}
          onChange={(e) =>
            setFilters({ ...filters, type: e.target.value })
          }
        >
          <option value="all">All</option>
          <option value="income">Income</option>
          <option value="expense">Expenses</option>
        </select>

        <input
          type="date"
          className="border rounded-md px-3 py-2 text-sm"
          value={filters.fromDate}
          onChange={(e) =>
            setFilters({ ...filters, fromDate: e.target.value })
          }
        />

        <input
          type="date"
          className="border rounded-md px-3 py-2 text-sm"
          value={filters.toDate}
          onChange={(e) =>
            setFilters({ ...filters, toDate: e.target.value })
          }
        />

        <div className="relative flex-1 min-w-[240px]">
          <Search className="absolute left-3 top-2.5 w-4 h-4 text-slate-400" />
          <input
            type="text"
            placeholder="Search description or payee"
            className="w-full border rounded-md pl-9 pr-3 py-2 text-sm"
            value={filters.search}
            onChange={(e) =>
              setFilters({ ...filters, search: e.target.value })
            }
          />
        </div>
      </div>
    </div>
<div className="bg-white rounded-xl shadow-sm border border-slate-200">
    <div className="max-h-[520px] overflow-y-auto">

  <table className="w-full text-sm">
     {/* HEADER */}
<thead className="bg-slate-50 border-b border-slate-200 sticky top-0 z-10">
      <tr className="text-slate-600">
        <th className="px-4 py-2 text-left font-semibold">Date</th>
        <th className="px-4 py-2 text-left font-semibold">Description</th>
        <th className="px-4 py-2 text-right font-semibold">Deposit</th>
        <th className="px-4 py-2 text-right font-semibold">Withdrawal</th>
        <th className="px-4 py-2 text-right font-semibold">Balance</th>
        <th className="px-4 py-2 text-center font-semibold">Actions</th>
      </tr>
    </thead>

          {/* BODY */}
          <tbody className="divide-y divide-slate-100">
            {filteredTransactions.map((t) => (
              <tr
                key={t.id}
                className="hover:bg-slate-50 transition"
              >
                <td className="px-4 py-2 text-slate-700 whitespace-nowrap">
                  {t.date}
                </td>

                <td className="px-4 py-2 text-slate-800">
                  {t.description}
                </td>

                <td className="px-4 py-2 text-right font-medium">
                  {t.deposit
                    ? <span className="text-emerald-700">{currency(t.deposit)}</span>
                    : <span className="text-slate-300">‚Äî</span>
                  }
                </td>

                <td className="px-4 py-2 text-right font-medium">
                  {t.withdrawal
                    ? <span className="text-rose-700">{currency(t.withdrawal)}</span>
                    : <span className="text-slate-300">‚Äî</span>
                  }
                </td>

                <td className="px-4 py-2 text-right font-semibold text-slate-900">
                  {currency(t.balance)}
                </td>

                <td className="px-4 py-2 text-center">
                  <div className="flex justify-center gap-2">
                    <button
                      onClick={() => startEdit(t)}
                      className="text-slate-400 hover:text-slate-700"
                      title="Edit"
                    >
                      <Edit3 size={15} />
                    </button>
                    <button
                      onClick={() => onDeleteTransaction(t.id)}
                      className="text-slate-400 hover:text-rose-600"
                      title="Delete"
                    >
                      <Trash2 size={15} />
                    </button>
                  </div>
                </td>
              </tr>
            ))}

            {!filteredTransactions.length && (
              <tr>
                <td
                  colSpan="6"
                  className="px-6 py-10 text-center text-slate-400"
                >
                  No transactions found
                </td>
              </tr>
            )}
          </tbody>
        </table>
        
    

      {loading && (
        <div className="p-4 text-center text-slate-500">
          Loading transactions‚Ä¶
        </div>
      )}
    </div>
  </div>
    </div>
);
}